tos sociales especialmente en los ambientes empresariales de producción y comercialización.")]),_c('p',{staticClass:"mb-3"},[_vm._v(" A lo largo del programa y las actividades que se deben realizar, permiten conocer de forma clara las herramientas para tomar la mejor decisión y encontrar la resolución de problemas tecnológicos con la capacidad de realizar una interfaz gráfica basada en el tipo de usuario, conectar la aplicación a una base de datos según el tipo de aplicación y generar las pruebas para el desarrollo de una aplicación que satisfaga las necesidades y que se elaboren productos de calidad para que se logre el despliegue del aplicativo en las tiendas de aplicaciones móviles como "),_c('i',[_vm._v("AppStore")]),_vm._v(" y "),_c('i',[_vm._v("PlayStore")]),_vm._v(". ")]),_c('p',{staticClass:"mb-3"},[_vm._v(" El perfil de desarrollador de aplicaciones móviles le permite no solo ser un buen programador, sino que, también podrá participar en el análisis de problemas y la selección de las herramientas tecnológicas más óptimas para implementar soluciones rápidas y eficientes reduciendo los tiempos y costos. Este programa con seguridad le permitirá trabajar como desarrollador independiente "),_c('i',[_vm._v("“FreeLancer”")]),_vm._v(" o de manera colaborativa en grupos de trabajo multidisciplinar para el desarrollo de aplicaciones móviles que permitan una modernización digital y contribuir con el desarrollo económico, social y tecnológico del país. ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"container tarjeta tarjeta__template--azul-claro p-4 p-md-5 mb-5",attrs:{"data-aos":"fade-up"}},[_c('div',{staticClass:"titulo__template--a mb-4"},[_c('span',{staticClass:"h4"},[_vm._v("2. Justificación del programa")])]),_c('p',{staticClass:"mb-3"},[_vm._v("Como parte de las estrategias del Estado Colombiano para mantener y mejorar el desarrollo económico, el Estado promovió la Ley 1834 de 2017 – Ley Naranja, la cual tiene como objeto desarrollar, fomentar, incentivar y proteger las industrias creativas. Estas son entendidas como aquellas industrias que generan valor en razón de sus bienes y servicios, los cuales se fundamentan en la propiedad intelectual. Integra medios digitales, diseño y publicidad para el desarrollo de contenidos que impulsen el emprendimiento y enfoque de los sectores económicos a diversos mercados.")]),_c('p',{staticClass:"mb-3"},[_vm._v(" El desarrollo de soluciones para dispositivos móviles se convierte en una gran herramienta para impulsar la economía naranja y las industrias creativas, pues utiliza las plataformas tecnológicas para llegar de manera innovadora a nuevos mercados y apertura de fronteras. El desarrollo para dispositivos móviles, además, guarda estrecha relación con tecnologías emergentes como la realidad virtual, aumentada y mixta y se integra de forma eficiente con la computación en la nube y la visión artificial, eslabones propios de la cuarta revolución industrial.")]),_c('p',{staticClass:"mb-3"},[_vm._v(" Los dispositivos móviles se han convertido en elementos permanentes de uso personal y empresarial. Por tal motivo, las aplicaciones móviles ocupan un importante lugar entre las herramientas de comunicación rápidas y eficientes. El creciente uso de dispositivos inteligentes ha provocado que las llamadas App, se conviertan en estrategias digitales para potenciar el modelo de negocios, conexión con las personas, las empresas y los grupos de valor. El desarrollo móvil se aplica de gran manera como canal de comunicación para el despliegue de contenido, educación, finanzas, compras y entretenimiento, entre otras actividades. ")]),_c('p',{staticClass:"mb-3"},[_vm._v(" Además, se requiere de la formación integral de recurso humano que permita aprovechar las ventajas ofrecidas por la Industria 4.0, los ambientes de programación modernos y las capacidades ofrecidas por los dispositivos móviles, de tal forma que se logre un despliegue ágil de aplicaciones.")]),_c('p',{staticClass:"mb-3"},[_vm._v(" Es fundamental la formación de técnicos con habilidades y destrezas en el uso y aprovechamiento de los entornos de programación para soluciones móviles, que permitan implementar aplicaciones modernas que permitan aprovechar las arquitecturas, sensores y componentes de los dispositivos actuales.")]),_c('p',{staticClass:"mb-3"},[_vm._v(" Programas que permitan al aprendiz, adquirir habilidades y desarrollar competencias usando diversos métodos para programar aplicaciones móviles versátiles, reconocer la diferencia entre aplicaciones nativas e híbridas, ofrecer arquitecturas livianas, que interactúen con las funcionalidades de los dispositivos móviles, potenciando el alcance de las soluciones. Diseños curriculares modernos que incorporen el uso de patrones de diseño, librerías y herramientas que para construir soluciones móviles de calidad y el uso de frameworks o entornos de programación con alto grado de reconocimiento, para las distintas plataformas tecnológicas como IOS o Android.")]),_c('p',{staticClass:"mb-3"},[_vm._v(" Se requiere entonces, el desarrollo de competencias tendientes al aprovechamiento de recursos tecnológicos, incorporando módulos de formación relacionados con la comprensión de requisitos de software, implementación de bases de datos y codificación de software para la construcción de soluciones ejecutables en dispositivo móviles. La transformación digital, modernización en las entidades públicas y privadas, así como la necesidad creciente en la industria creativa y economía naranja, requieren de profesionales con habilidades y destrezas para la programación de software que aproveche al máximo los recursos y arquitecturas móviles.")]),_c('p',{staticClass:"mb-3"},[_vm._v(" Es fundamental de acuerdo con la Misión el SENA, apoyar en el cierre de brechas digitales del país, disminuyendo el déficit de profesionales en la programación de software para dispositivos móviles, así como promover e